:root {
  --white: #ffffff;
  --white-30: rgba(255, 255, 255, 0.3);
  --black: #000000;
  --primary: #de0000;
  --secondary: #207376;
  --offset-a1: 250px;
  --offset-a2: 130px;
  --offset-a3: 100px;
  --offset-a4: 50px;
  --offset-a5: 30px;
  --offset-a6: 20px;
  --h1-size: 72px;
  --h2-size: 40px;
  --h3-size: 36px;
  --subhead-size: 20px;
  --text-body-size: 18px;
  --text-body-accent-size: 18px;
  --additional-text-size: 22px;
  --text-info-size: 12px;
}

body {
  font-family: "Geologica", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  background-color: var(--black);
  color: var(--white);
  font-style: normal;
  font-variation-settings: "slnt" 0, "CRSV" 0, "SHRP" 0;
  overflow-x: clip;
}

.overflow-hidden {
  overflow: hidden;
  max-height: 100dvh;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
}

section {
  overflow-x: clip;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  background-color: transparent;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

input {
  background: unset;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
}

.h1 {
  font-size: var(--h1-size);
  font-weight: 700;
  font-family: "Geologica", sans-serif;
}

.h2 {
  font-size: var(--h2-size);
  font-weight: 700;
  font-family: "Geologica", sans-serif;
}

