  font-size: var(--h3-size);
  font-weight: 700;
  font-family: "Geologica", sans-serif;
}

.subhead {
  font-size: var(--subhead-size);
  font-weight: 700;
  font-family: "Geologica", sans-serif;
}

.text-body {
  font-size: var(--text-body-size);
  font-weight: 100;
  font-family: "Geologica", sans-serif;
  line-height: 1.25;
}

.text-body-accent {
  font-size: var(--text-body-accent-size);
  font-weight: 700;
  font-family: "Geologica", sans-serif;
}

.additional-text {
  font-size: var(--additional-text-size);
  font-weight: 400;
  font-family: "Geologica", sans-serif;
}

.text-info {
  font-size: var(--text-info-size);
  font-weight: 400;
  font-family: "Geologica", sans-serif;
}

