@media screen and (max-width: 768px) {
  :root {
    --h1-size: 40px;
    --h2-size: 24px;
    --h3-size: 24px;
    --subhead-size: 14px;
    --text-body-size: 14px;
    --text-body-accent-size: 14px;
    --additional-text-size: 18px;
    --text-info-size: 10px;
    --offset-a1: 140px;
    --offset-a2: 90px;
    --offset-a3: 80px;
    --offset-a4: 40px;
    --offset-a5: 30px;
    --offset-a6: 20px;
  }
}
@media screen and (max-width: 1198.5px) {
  .container {
    padding-left: 9.25px;
    padding-right: 9.25px;
  }
  .lines {
    width: calc(100dvw - 18.5px);
  }
  .lines__line {
    width: 1.1px;
  }
  .lines__line:first-child {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .line-splitter--mobile {
    display: block;
  }
  .line-splitter--desktop {
    display: none;
  }
  .ellipse--mobile {
    display: block;
  }
  .ellipse--desktop {
    display: none;
  }
  .header__container {
    position: relative;
  }
  .header__burger {
    display: inline-flex;
  }
  .header__button--mobile {
    display: inline-flex;
    align-self: flex-end;
    justify-content: space-between;
    margin-top: auto;
    margin-bottom: 45px;
    width: calc(66% + 2px);
  }
  .header__button--desktop {
    display: none;
  }
  .header__menu {
    position: absolute;
    display: flex;
    flex-direction: column;
    max-width: 100dvw;
    max-height: 100%;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 20;
    transition: all 1.3s ease-in-out;
    transform: translateY(64px);
  }
  .header__menu.active .nav__list {
    height: calc(100dvh - 64px);
  }
  .nav {
    width: 100%;
    padding-left: 9.25px;
    padding-right: 9.25px;
    background-color: var(--black);
  }
  .nav__list {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    height: 0;
    gap: 0;
    transition: height 0.3s ease-in-out;
  }
  .nav__item {
    padding: 25px 0;
    width: 100%;
    font-size: 20px;
    line-height: 125%;
    text-transform: uppercase;
    border-bottom: 1px solid var(--white);
  }
  .nav__item:first-child {
    margin-top: 25px;
    border-top: 1px solid var(--white);
  }
  .hero__slogan {
    margin-top: 38px;
    max-width: 255px;
  }
  .hero__slogan-inner {
    padding: 19px 6px 19px 29px;
  }
  .slogan__hexagons {
    max-width: 258px;
    top: 25px;
  }
  .slogan__ellipse {
    top: unset;
    right: unset;
    transform: unset;
    left: -28px;
    bottom: 0;
  }
  .slogan__title-element:nth-child(3) {
    margin-left: 25%;
  }
  .slogan__author {
    margin-left: 33%;
    margin-right: unset;
    width: 67%;
  }
  .history__subsection {
    flex-direction: column;
    gap: var(--offset-a4);
  }
  .history__image {
    max-width: 100%;
    clip-path: polygon(4px 0, 100% 0, 100% calc(100% - 4px), calc(100% - 4px) 100%, 0 100%, 0 4px);
  }
  .history__info {
    max-width: 100%;
  }
  .history__info-inner {
    padding-bottom: 30px;
  }
  .team__info {
    max-width: 100%;
  }
  .team__info-hexagons {
    right: -15px;
    top: unset;
    bottom: -190px;
    width: 245px;
  }
  .team__info-inner {
    margin-left: 20px;
  }
  .player-preview__image {
    clip-path: polygon(4px 0, 100% 0, 100% calc(100% - 4px), calc(100% - 4px) 100%, 0 100%, 0 4px);
  }
  .solution__ellipse {
    width: 231px;
    height: 231px;
  }
  .solution__ellipse--second {
    top: 60px;
    left: 150px;
  }
  .solution__ellipse-container {
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  .solution__slider-controls--mobile {
    display: inline-flex;
  }
  .solution__slider-wrapper {
    display: block;
  }
  .solution__slider-wrapper .slick-list {
    overflow: unset !important;
  }
  .solution__slider-wrapper .slick-list .slick-track {
    display: flex !important;
    gap: 15px;
  }
  .solution__title {
    max-width: 100%;
    margin-bottom: 8px;
  }
  .solution__slide {
    max-width: 315px;
  }
  .solution__image {
    clip-path: polygon(8px 0, 100% 0, 100% calc(100% - 8px), calc(100% - 8px) 100%, 0 100%, 0 8px);
  }
  .details__title {
    text-align: left;
  }
  .details__info::before, .details__info::after {
    display: none;
  }
  .details__info-image {
    max-width: 200px;
    top: -25%;
    left: unset;
    right: 0;
    transform: translateY(25%);
  }
  .details__info-row {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
  .details__info-block--left, .details__info-block--right {
    text-align: left;
  }
  .details__info-inner {
    padding: unset;
  }
  .details__info-inner::before, .details__info-inner::after {
    display: none;
  }
  .details__info-inner--mobile {
    display: flex;
  }
  .details__info-inner--desktop {
    display: none;
  }
  .details__category {
    font-size: 12px;
    letter-spacing: 2.76px;
  }
  .details__body {
    font-size: 16px;
  }
  .details__comment {
    font-size: 10px;
    font-weight: 500;
    max-width: 50%;
  }
  .advantages__title--desktop {
    display: none;
  }
  .advantages__title--mobile {
    display: block;
  }
  .advantages__slider > .advantages__slide {
    display: none;
  }
  .advantages__slide {
    padding: 32px 0;
    flex-shrink: 0;
    max-width: 265px;
    height: auto;
  }
  .advantages__slide-inner {
    padding: 0 25px 0 20px;
  }
  .advantages__slide-inner::before {
    content: "";
    position: absolute;
    top: 32px;
    left: 0;
    width: 3px;
    height: 32px;
    background-color: var(--primary);
  }
  .advantages__slider {
    display: flex;
    flex-direction: column;
    flex-wrap: unset;
  }
  .advantages__slider-wrapper {
    display: block;
  }
  .advantages__slider-wrapper .slick-list {
    overflow: unset !important;
  }
  .advantages__slider-wrapper .slick-list .slick-track {
    display: flex !important;
    gap: 15px;
  }
  .advantages__slider-controls--mobile {
    display: inline-flex;
    justify-self: flex-end;
  }
  .advantages__text {
    font-size: 14px;
    font-weight: 100;
  }
}
.line-splitter {
  display: block;
  width: 0;
  height: 0;
}
.line-splitter--mobile {
  display: none;
}

.lines {
  position: fixed;
  top: 0;
  left: 50%;
  right: 0;
  bottom: 0;
  width: 1180px;
  transform: translateX(-50%);
  min-height: 100dvh;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  pointer-events: none;
  z-index: 1;
}
.lines__line {
  background-color: rgba(184, 184, 184, 0.1);
  width: 1px;
  height: 100%;
}
.lines.active {
  z-index: 25;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 18px;
  padding: 16px 24px;
  border: 1px solid transparent;
  background-color: transparent;
  color: var(--white);
  text-transform: uppercase;
  font-family: "Geologica", sans-serif;
  font-size: 12px;
  font-weight: 400;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.button__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 10px;
  height: 10px;
  transition: transform 0.3s ease-in-out;
}
.button__icon svg {
  width: 100%;
  height: 100%;
  color: var(--white);
  transition: color 0.3s ease-in-out;
}
.button--primary {
  position: relative;
  padding: 15px 15px 15px 25px;
  justify-content: flex-start;
  border-left-color: transparent;
}
.button--primary::after {
  content: "";
  position: absolute;
  width: 3px;
  background-color: var(--primary);
  height: 100%;
  left: -1px;
  top: 52.5%;
  transform: translateY(-50%);
  transition: height 0.3s ease-in-out;
}
.button--primary:hover {
  border-color: var(--white-30);
  color: white;
}
.button--primary:hover::after {
  height: calc(100% + 1.34px);
}
.button--primary:hover .button__icon {
  transform: rotate(90deg);
}
.button--primary:hover .button__icon svg {
  color: var(--primary);
}
.button--menu {
  position: relative;
  padding: 10px 20px 10px 18px;
  border-right-color: transparent;
}
.button--menu::after {
  content: "";
  position: absolute;
  width: 3px;
  background-color: var(--primary);
  height: calc(100% - 12px);
  right: -1px;
  top: 52.5%;
  transform: translateY(-50%);
  transition: height 0.3s ease-in-out;
}
.button--menu:hover {
  border-color: var(--white-30);
  color: white;
}
.button--menu:hover::after {
  height: calc(100% + 1.34px);
}
.button--menu:hover .button__icon {
  transform: rotate(90deg);
}
.button--menu:hover .button__icon svg {
  color: var(--primary);
}

@media screen and (max-width: 768px) {
  .button {
    padding: 12px 20px;
    font-size: 16px;
  }
  .button__icon {
    width: 20px;
    height: 20px;
  }
}
