}
.input-field.error .input-field__label, .error.contact__form-field .input-field__label {
  opacity: 0;
}
.input-field.error .input-field__error, .error.contact__form-field .input-field__error {
  opacity: 1;
}
.input-field.error .input-field__input, .error.contact__form-field .input-field__input {
  border-color: var(--primary);
  color: var(--primary);
}
.input-field__input {
  width: 100%;
