  padding: 24px 0 8px;
  background: transparent;
  border-bottom: 1px solid var(--white);
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  transition: all 0.2s ease-in-out;
}
.input-field__input::placeholder {
  color: transparent;
}
.input-field__input:focus {
  border-color: #fff;
  outline: none;
}
.input-field__input:focus + .input-field__label {
  top: 15px;
  font-size: 10px;
  color: #fff;
}
.input-field__input:focus + .input-field__label + .input-field__error {
  top: 15px;
  font-size: 10px;
}
.input-field__input:not(:placeholder-shown) + .input-field__label {
  top: 15px;
  font-size: 10px;
  color: rgba(255, 255, 255, 0.5);
}
.input-field__input:not(:placeholder-shown) + .input-field__label + .input-field__error {
  top: 15px;
  font-size: 10px;
}
